.formControls.error input,
.formControls.error textarea {
    border: red solid 1px;
}

.formControls.error span {
    margin-left: 20px;
    color: red;
}

.formControls {
    display: flex;
    flex-direction: column;
}
input[type='checkbox'] {
    width: auto;
align-self: flex-start;
}